
body{margin:0;padding:0
}
#App{font-family:"Anaheim",sans-serif;-webkit-font-smoothing:antialiased;color:#151515;margin:0;padding:0
}
h1,h2{font-family:"Quattrocento Sans",sans-serif
}
a{color:#151515;text-decoration:none;transition:color 0.2s
}
a:hover{color:#06b3e6
}
.col{float:left;display:table-column;width:200px
}
#LoginScreen{width:60%;font-size:20px;position:absolute;top:48.54%;left:50%;transform:translateX(-50%) translateY(-50%);display:table
}
#LoginScreen .row{display:table-row;width:auto;clear:both
}
#LoginScreen .welcome{margin-top:2rem;width:60%;display:inline-block;font-size:16px
}
#LoginScreen .welcome .pad{padding:0px 40px
}
#LoginScreen .form{display:inline-block;width:40%
}
#LoginScreen h1{font-size:2.375rem;text-align:left;border-bottom:1px solid rgba(21,21,21,0.05);padding:0 0 0.5rem 1rem;margin:0 0 1rem
}
#LoginScreen h2{font-size:1.375rem;margin-bottom:30px
}
#LoginScreen .error,#LoginScreen .downloading{text-align:center;margin-top:30px;font-size:1rem
}
#LoginScreen .error{color:#a94442
}
#LoginScreen input,#LoginScreen input[type="checkbox"]{font-family:"Anaheim",sans-serif;border:1px solid rgba(21,21,21,0.1);border-radius:2px;transition:all ease-in-out 0.15s
}
#LoginScreen .form-input.first{margin-top:5px
}
#LoginScreen .form-input input:focus,#LoginScreen input[type="checkbox"]:focus,#LoginScreen button:focus{outline:none;border-color:#00b3e6;box-shadow:0 0 5px #00b3e6
}
#LoginScreen .form-input input{display:inline-block;width:90%;height:34px;padding:6px 12px;font-size:14px;line-height:1.42857143;color:#555;background-color:#fff;background-image:none;height:20px
}
#LoginScreen .form-input input.disabled{color:rgba(85,85,85,0.5)
}
#LoginScreen .form-input.remember{text-align:left;margin-top:2rem
}
#LoginScreen .form-input.remember span{font-size:1rem
}
#LoginScreen .form-input.remember input{height:13px
}
#LoginScreen .form-input.remember span,#LoginScreen .form-input.remember input{width:auto
}
#LoginScreen .form-input.remember button{background-color:#06b3e6;color:#fff;font-family:"Quattrocento Sans",sans-serif;border:1px solid transparent;border-radius:0;display:inline-block;font-size:0.8em;padding:4px 1em;text-align:center;cursor:pointer;margin-right:10px;float:right;transition:all ease-in-out 0.15s
}
#LoginScreen .form-input.remember button:hover{background-color:rgba(6,179,230,0.8)
}
.providerchoice{margin:0px;font-size:2em;text-align:center
}
.providerchoice span{font-size:0.4em;margin:0 20px
}
.providerchoice i{transition:color 0.2s;cursor:pointer;color:rgba(21,21,21,0.3)
}
.providerchoice i.selected{color:#151515
}
.helper{text-align:left;font-size:0.62em;margin:-17px 0 0 5px
}
.helper a{border-bottom:1px dotted rgba(21,21,21,0.2)
}
.helper a:hover{text-decoration:none
}
.helper.disabled{color:rgba(21,21,21,0.5)
}
.more{border-top:1px solid rgba(21,21,21,0.05);margin-top:1rem
}
.col.copy,.col.social{font-family:"Quattrocento Sans",sans-serif;font-size:0.75em
}
.col.copy .pad,.col.social .pad{padding:0.5rem 0 0 1rem
}
.col.copy{font-weight:700;width:60%;display:inline-block
}
.col.copy i{margin-left:5px
}
.copyright{font-family:"Anaheim",sans-serif;font-weight:normal;font-size:0.75rem;margin-top:-12px
}
.col.social{font-family:"Anaheim",sans-serif;font-size:0.875rem;text-align:right;display:inline-block;width:40%
}
.col.social p.first{margin-top:14px
}
.col.social p{margin:0;line-height:21px
}
#top{margin:0;background-color:#fafafa;top:0;left:0;height:26px;line-height:26px
}
#top p{margin:0px
}
#top .server i{cursor:pointer;margin-left:10px
}
#top .server{position:absolute;right:10px
}
#top .user{font-family:"Quattrocento Sans",sans-serif
}
#top .user img{vertical-align:middle;margin-right:10px;height:26px
}
.standardpadding{padding:10px
}
.fade-enter-active,.fade-leave-active{transition:opacity .5s
}
.fade-enter,.fade-leave-active{opacity:0
}
.pagination{text-align:center
}
.pagination span{margin:0 20px
}
.perpage{margin-top:20px;text-align:center;font-size:0.8em
}
button{border:1px solid #bbb;padding:2px 4px 0;margin:0;font:inherit;outline:none;line-height:1.2;background:#f8f8f8;border-radius:4px;cursor:pointer
}
.downloading{text-align:center;font-size:2.5em
}
.provider{font-family:"Quattrocento Sans",sans-serif
}
.provider input[type=radio]{display:none
}
.provider input[type=radio]+label{display:inline-block;margin:-2px;padding:4px 12px;margin-bottom:0;text-align:center;vertical-align:middle;cursor:pointer;border-bottom:2px solid #fafafa;color:#777
}
.provider input[type=radio]:checked+label{border-bottom:2px solid #06b3e6;color:#2c3e50
}
.refresh{float:right;position:absolute;right:10px;font-size:0.8em;cursor:pointer
}
.filter{padding:8px;margin:0;background-color:#fafafa;border-bottom:1px solid #e5e5e5;top:0;left:0;height:26px;line-height:26px;text-align:center
}
.subfilter{margin:0;padding:8px;background-color:#f5f5f5;border-bottom:1px solid #e5e5e5;top:0;left:0;height:41px;line-height:26px;text-align:center
}
.multiselect-container{width:300px;display:inline-block
}
.multiselect__spinner:before,.multiselect__spinner:after{border-color:#06b3e6 transparent transparent !important
}
.multiselect__tag{background:#06b3e6 !important
}
.multiselect__option--highlight{background:#06b3e6 !important
}
.multiselect__option--highlight:after{background:#06b3e6 !important
}
.group-separator{font-weight:bold;font-size:1.5em
}
.alert{padding:15px;margin-bottom:20px;border:1px solid transparent;border-radius:4px
}
.alert-danger{color:#a94442;background-color:#f2dede;border-color:#ebccd1
}
#Gantt{font-family:"Anaheim",sans-serif
}
.rect_has_data{fill:#5cb85c
}
.rect_has_data:hover{fill:#449d44
}
.rect_has_no_data{fill:#d9534d
}
.rect_has_no_data:hover{fill:#c9302c
}
.taskTooltip_has_data{color:#449d44
}
.taskTooltip_has_no_data{color:#c9302c
}
div.taskTooltip{font-family:"Anaheim",sans-serif;position:absolute;text-align:left;-moz-osx-font-smoothing:grayscale;font-size:10px;padding-left:0;width:auto;border:0;border-left:thin solid #000000;pointer-events:none;line-height:12px;padding-top:0;display:block
}
.x_tick_emph{font-weight:bold
}
.x_tick_today{font-weight:bold
}
.x_tick_today text{fill:#ff0000 !important
}
.ytitle{dominant-baseline:middle;font-family:"Quattrocento Sans",sans-serif;-moz-osx-font-smoothing:grayscale;font-size:12px
}
.axis path,.axis line{display:none
}
.axis text{font-size:12px;-moz-osx-font-smoothing:grayscale;fill:#777
}
.vert_grid{fill:none;stroke:#dddddd;stroke-width:1px
}
.vert_grid_emph{fill:none;stroke:#dddddd;stroke-width:2px
}
.vert_grid_today{fill:none;stroke:#ff0000;stroke-width:2px
}
.horz_grid{fill:none;stroke:#dddddd;stroke-width:1px
}
.heading{font-size:16px;-moz-osx-font-smoothing:grayscale;font-weight:bold
}
.subheading{font-size:12px;-moz-osx-font-smoothing:grayscale;fill:#777
}
.legend{dominant-baseline:middle;font-size:12px;-moz-osx-font-smoothing:grayscale;fill:#777
}



